import React, { Component } from 'react';
// import Papers from '../Papers';

const Papers = {
  Test: require('./NuReco.pdf'),
  NuReco: "https://arxiv.org/abs/2006.03027",
  WhitePaper: "https://arxiv.org/abs/2004.09841",
  NuVertex: "https://arxiv.org/abs/1909.02677",
  NuRadioReco: "https://arxiv.org/abs/1903.07023",
  UHEnu: "https://arxiv.org/abs/1903.01609",
  EWBG: "https://arxiv.org/abs/1810.02522",
  HorizontalProp: "https://arxiv.org/abs/1804.10430",
  WhitePaper: "",
  DodgerMan3000: "https://apps.apple.com/us/app/id1525517799",
  TapThat: "https://apps.apple.com/us/app/tap-that-multi-button-game/id1514845531",
  MyBestFriend: "https://apps.apple.com/us/app/my-best-friend-comfort/id1518660881",
}

class Portfolio extends Component {
  render() {

    if (this.props.data) {
      var projects = this.props.data.projects.map(function (projects) {
        var projectImage = 'images/portfolio/' + projects.image;
        return <div key={projects.title} className="columns portfolio-item">
          <div className="item-wrap">
            <a href={Papers[projects.name]} title={projects.title}>
              <img alt={projects.title} src={projectImage} />
              <div className="overlay">
                <div className="portfolio-item-meta">
                  <h5>{projects.title}</h5>
                  <p>{projects.category}</p>
                </div>
              </div>
              <div className="link-icon"><i className="fa fa-link"></i></div>
            </a>
          </div>
        </div>
      })
    }

    return (
      <section id="portfolio">

        <div className="row">

          <div className="twelve columns collapsed">

            <h1>Check Out Some of My Publications.</h1>

            <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
              {projects}
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Portfolio;
